<script>

import PostsService from '@/services/PostsService'
import Vue from 'vue'
import VeeValidate from 'vee-validate'

Vue.use(VeeValidate)

VeeValidate.Validator.extend('passphrase', {
  getMessage: field => 'Sorry dude, wrong pass phrase.',
  validate: value => value.toUpperCase() === 'Demogorgon'.toUpperCase()
})

export default {
  name: 'person',
  data () {
    return {
      pai: {},
      datos: [],
      datos1: [],
      datos2: [],
      datos3: [],
      datos4: [],
      datos5: ['LP', 'CH', 'CB', 'OR', 'PO', 'TJ', 'SC', 'BE', 'PA'],
      ci: '',
      nombre: '',
      paterno: '',
      materno: '',
      domicilio: '',
      correo: '',
      celular: '',
      sexo: '',
      ts: '',
      ec: '',
      picked: '',
      exp: '',
      numero: 0,
      tipo: '',
      a√±o_anti: '',
      perso: '',
      a_ingreso: '',
      a_curri: '',
      a_des: '',
      p_curri: '',
      p_grado: '',
      p_anti: '',
      e_rude: '',
      e_ingreso: '',
      errors1: [],
      nombre1: JSON.parse(localStorage.getItem('jwtToken')).data.user,
      email: '',
      name: '',
      url: '',
      secret: '',
      formSubmitted: false
    }
  },
  mounted () {
    this.getdatos()
  },
  methods: {
    async getdatos () {
      const response = await PostsService.mostrardatos()
      this.datos = response.data.pais
      this.datos3 = response.data.ts
      this.datos4 = response.data.ec
      console.log(this.datos1)
    },
    checkForm: function (e) {
      this.errors1 = []
      if (!this.name) {
        this.errors1.push('Name required.')
      }
      if (!this.email) {
        this.errors1.push('Email required.')
      } else if (!this.validEmail(this.email)) {
        this.errors1.push('Valid email required.')
      }
      if (!this.errors1.length) {
        return true
      }
      e.preventDefault()
    },
    validEmail: function (email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(email)
    },
    async depto ({type, target}) {
      if (target.value === '0') {
        this.datos1 = []
        this.datos2 = []
      } else {
        this.datos2 = []
        const response = await PostsService.mostrardepto(target.value)
        this.datos1 = response.data.depto
        console.log(this.datos1)
      }
    },
    async prov ({type, target}) {
      const response = await PostsService.mostrarprov(target.value)
      this.datos2 = response.data.prov
      console.log(this.datos2)
    },
    async tip ({type, target}) {
      console.log(this.numero)
      if (target.value === '1') {
        this.numero = 1
      }
      if (target.value === '2') {
        this.numero = 2
      }
      if (target.value === '3') {
        this.numero = 3
      }
    },
    async adduser () {
      if (!this.correo) {
        this.errors1.push('adicione correo')
      } else if (!this.validEmail(this.correo)) {
        this.errors1.push('correo invalido')
      } else if (!this.nombre) {
        this.errors1.push('adicione nombre')
      } else if (!this.paterno) {
        this.errors1.push('adicione apellido paterno')
      } else if (!this.materno) {
        this.errors1.push('adicione apellido materno')
      } else if (this.celular.length < 8) {
        this.errors1.push('celular incorrecto')
      } else {
        console.log(this.picked)
        console.log('este es el nombre')
        await PostsService.adduser({
          ci: this.ci,
          nombre: this.nombre,
          ex: this.exp,
          paterno: this.paterno,
          materno: this.materno,
          domicilio: this.domicilio,
          correo: this.correo,
          celular: this.celular,
          pais: this.pai.pais,
          depto: this.pai.depto,
          prov: this.pai.prov,
          sexo: this.picked,
          ts: this.ts,
          ec: this.ec,
          tipo: this.tipo,
          a_curri: this.a_curri,
          a_des: this.a_des,
          a_ingreso: this.a_ingreso,
          p_anti: this.p_anti,
          p_curri: this.p_curri,
          p_grado: this.p_grado,
          e_rude: this.e_rude,
          e_ingreso: this.e_ingreso
        })
        this.$swal(
          'El Usuario se creo',
          `Satisfactoriamente`,
          'success'
        )
        this.$router.push({ name: 'admin' })
      }
    },
    logout () {
      localStorage.removeItem('jwtToken')
      this.$router.push({
        name: 'login'
      })
    }
  }
}

</script>
