<script>
import PostsService from '@/services/PostsService'
export default {
  name: 'inscripcion',
  data () {
    return {
      datos: [],
      datos3: [],
      datos1: [],
      datos2: [],
      nombre1: JSON.parse(localStorage.getItem('jwtToken')).data.user,
      id: JSON.parse(localStorage.getItem('jwtToken')).data.id,
      ci_est: '',
      ci_tut: '',
      fecha: '',
      obs: '',
      est1: '',
      est2: '',
      datos4: []
    }
  },
  mounted () {
    this.getdatos()
  },
  methods: {
    async getdatos () {
      const response = await PostsService.mostrarotros()
      this.datos3 = response.data.niv
      this.datos1 = response.data.profes
      console.log(this.datos3)
    },
    async mosins ({type, target}) {
      this.datos = []
      const response = await PostsService.mosins(target.value)
      this.datos = response.data.ins
      this.datos2 = response.data.niv
      console.log(this.datos)
    },
    async notas ({type, target}) {
      this.datos4 = []
      const response = await PostsService.mosnotas(target.value)
      this.datos4 = response.data.not
      console.log(this.datos4)
    },
    logout () {
      localStorage.removeItem('jwtToken')
      this.$router.push({
        name: 'login'
      })
    }
  }
}
