import estnotas from '@/components/est/estnotas'
import compiprof from '@/components/prof/compiprof'
import tutor from '@/components/admin/tutor'
import tutorest from '@/components/admin/tutorest'
import impusu from '@/components/admin/impusu'
import impins from '@/components/admin/impins'

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'home',
    component: home
  },
  {
    path: '/login',
    component: login,
    name: 'login'
  },
  {
    path: '/mivi',
    component: mivi,
    name: 'mivi'
  },
  {
    path: '/contra',
    component: contra,
    name: 'contra'
  },
  {
    path: '/quienes',
    component: quienes,
    name: 'quienes'
  },
  {
    path: '/historia',
    component: historia,
    name: 'historia'
  },
  {
    path: '/post',
    component: post,
    name: 'post'
  },
  {
    path: '/luis',
    component: luis,
    name: 'luis'
  },
  {
    path: '/otro',
    component: otro,
    name: 'otro'
  },
  {
    path: '/admin',
    component: admin,
    name: 'admin',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/impusu',
    component: impusu,
    name: 'impusu',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/impins',
    component: impins,
    name: 'impins',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/tutor',
    component: tutor,
    name: 'tutor',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/tutorest',
    component: tutorest,
    name: 'tutorest',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/compi',
    component: compi,
    name: 'compi',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/inscripcion',
    component: inscripcion,
    name: 'inscripcion',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/notas',
    component: notas,
    name: 'notas',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/addnotas',
    component: addnotas,
    name: 'addnotas',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/inscrip',
    component: inscrip,
    name: 'inscrip',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/editmateria',
    component: editmat,
    name: 'editmateria',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/crearmateria',
    component: crearmateria,
    name: 'crearmateria',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/materias',
    component: materias,
    name: 'materias',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/vistausera',
    component: vistausua,
    name: 'vistausera',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/profesor',
    component: profe,
    name: 'prof',
    meta: {requiresAuth: true, residentAuth: true, adminAuth: false, residentAuth1: false}
  },
  {
    path: '/compiprof',
    component: compiprof,
    name: 'compiprof',
    meta: {requiresAuth: true, residentAuth: true, adminAuth: false, residentAuth1: false}
  },
  {
    path: '/vistaprof',
    component: vistaprof,
    name: 'vistaprof',
    meta: {requiresAuth: true, residentAuth: true, adminAuth: false, residentAuth1: false}
  },
  {
    path: '/obser',
    component: obser,
    name: 'obser',
    meta: {requiresAuth: true, residentAuth: true, adminAuth: false, residentAuth1: false}
  },
  {
    path: '/estudiante',
    component: est,
    name: 'est',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/estnotas',
    component: estnotas,
    name: 'estnotas',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/vistaest',
    component: vistaest,
    name: 'vistaest',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/observacionest',
    component: observacionest,
    name: 'observacionest',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/horarioest',
    component: horarioest,
    name: 'horarioest',
    meta: {requiresAuth: true, adminAuth: false, residentAuth: false, residentAuth1: true}
  },
  {
    path: '/user/add',
    component: adduser,
    name: 'adduser',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/user',
    component: user,
    name: 'user',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/editusu',
    component: editusu,
    name: 'editusu',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/hor/add',
    component: horadd,
    name: 'horadd',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  },
  {
    path: '/horario',
    component: horario,
    name: 'horario',
    meta: {requiresAuth: true, adminAuth: true, residentAuth: false, residentAuth1: false}
  }
]

const router = new Router({routes, mode: 'history'})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    const authUser = JSON.parse(localStorage.getItem('jwtToken'))
    if (!authUser || !authUser.token) {
      console.log('paso un error de luis')
      next({name: 'Login'})
    } else if (to.meta.adminAuth) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 1) {
        console.log('es de tipo luis')
        next()
      } else {
        if (authUser.data.tipo === 2) {
          next({name: 'prof'})
        }
        if (authUser.data.tipo === 3) {
          next({name: 'est'})
        }
      }
    } else if (to.meta.residentAuth) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 2) {
        next()
      } else {
        if (authUser.data.tipo === 1) {
          next({name: 'admin'})
        }
        if (authUser.data.tipo === 3) {
          next({name: 'est'})
        }
      }
    } else if (to.meta.residentAuth1) {
      const authUser = JSON.parse(localStorage.getItem('jwtToken'))
      if (authUser.data.tipo === 3) {
        next()
      } else {
        if (authUser.data.tipo === 2) {
          next({name: 'prof'})
        }
        if (authUser.data.tipo === 1) {
          next({name: 'admin'})
        }
      }
    }
  } else {
    next()
  }
})

export default router



